.footer{
    color: white;
    background-color:#232F3E ;
    padding: 50px 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.amazonImg{
    margin-top: 40px;
    display: flex;
    gap: 100px;
    
}
.amazonImg div{
    border: 1px solid rgba(255, 255, 255, 0.523);
    padding-left:10px;
    padding-right: 60px;
    border-radius: 3px;
    margin-top: -10px;
    margin-bottom: 15px;
    cursor: pointer;
    &:hover{
        border: 1px solid rgba(255, 255, 255, 0.622);
    }
}
.amazonImg div img{
    margin-top: 10px;
}
.amazonImg div span{
    font-size: 13px;
    margin-left: 10px;
    margin-top: 5.5px;
    position:absolute;
}
.amazonImgFooter{
    width: 110px;
}
.footerContent{
    display: flex;
    justify-content: space-between;
    width: 70%;
    flex-wrap: wrap;
    gap: 50px;
}
.contentFooterTitle{
    font-weight: 600;
}
.contentFooterSubTitlediv{
    margin-top: 10px;
    font-size: 14px;
}
.contentFooterSubTitleCont{
    margin-bottom: 10px;
    cursor: pointer;
    &:hover{
        text-decoration: underline;
    }
}

@media screen and (max-width:350px) {
    .amazonImg{
        margin-top: 40px;
        display: flex;
        gap: 50px;
        
    }
}