.navbarBanner{
    background-color: #232f3e;
    padding: 0 0 0 11px;
    height: 41px;
    color: white;
}
.navbarBanner1{
    background-color: #232f3e;
    padding: 0 12px 100px 12px;
    max-height: 100%;
    color: white;
    width: 15%;
    z-index: 100;
    position: absolute;
  display: none;


   
}
.navbarBannerOptionsLeft{
    display: flex;
}
.optionsNavbarBanner{
    display: flex;
    padding: 8px 9px;
    justify-content: center;
    text-decoration: none;
    color: white;
    align-items: center;
    cursor: pointer;
    &:hover{
        border: 1px solid;
        border-radius: 3px;
    }
}
.optionsNavbarBanner1{
    display: flex;
    padding: 8px 9px;
    justify-content: left;
    text-decoration: none;
    color: white;
    cursor: pointer;
    &:hover{
        border: 1px solid;
        border-radius: 3px;
    }
}

.allOptionsNavbarBanner{
    font-size: 14px;
    padding-left: 2px;
    padding-bottom: 2px;
    font-weight: 500;
}

@media(max-width:1500px){
    .item{
        display: none;
    }
}
@media(max-width:1105px){
    .navbarBanner1{
        width: 200px;
    }
}