.navbarBuild{
    background-color: #131921;
    display: flex;
    width: 100%;
    height: 60px;
    color: white;
    box-sizing: border-box;
}
.leftNavbar{
    display: flex;
    gap: 10px;
    padding-left: 10px;
    padding-top: 5px;
    margin-right:10px ;
}
.leftNavbuildLogo{
    width: 110px;
    height: 30px;
    text-decoration: none;
    color: white;
    display: flex;
    padding: 8px;
    cursor: pointer;
    &:hover{
        border: 1px solid;
        border-radius: 3px;
    }
}
.amazonLogoNavbar{
    width: 97px;
    height: 120%;
    margin-top: 2px;
    
}
.navbar_inLogo{
    margin-left: 2px;
    font-weight: 400;
}

/* -------- */

.navbarBuildLocation{
    display: flex;
    gap: 5px;
    padding: 8px;
    height: 30px;
    cursor: pointer;
    &:hover{
        border-radius: 3px;
        border: 1px solid;
    }
}
.navbarBuildLocationImg{
    width: 15px;
    padding-top: 7px;
}
.navbarBuildLocationImgIcon{
    margin-top: 2px;
}
.navbarBuildLocationPlace{
    display: flex;
    flex-direction: column;
}
.navbarBuildLocationTop{
    font-size: 12px;
    color: #ccc;
    letter-spacing: 0.5px;
    font-weight: 500;
}
.navbarBuildLocationBottom{
    font-size: 14px;
    font-weight: 700;
    line-height: 10px;
}

/* ------ */

.navbarBuildSearchBox{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px 4px;
    width: 50%;
}
.navbarBuildSearchDiv{
    display: flex;
    width: 100%;
    height: 40px;
    border-radius: 5px;
    border: 1px solid #131921;
}
.navbarBuildSearchBoxAll{
    display: flex;
    background-color: #dbdbdb;
    gap: 2px;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    justify-content: center;
    align-items: center;
    padding-left:10px;
    cursor: pointer;
    color: #131921b5;
    &:hover{
        background-color: #cdcdcd;
        color:black;
        transition: all 0.3s ease;
    }
}
.navbarBuildSearchBoxAllText{
    font-size: 12px;
    font-weight: 600;
}

.navbarBuildInputSearchBox{
    padding: 5px;
    width: 84%;
    border: none;
    &::placeholder{
        color: gray;
        font-size: 15px;
        font-weight: 500;
    }
    &:focus{
        outline: none;
    }
}

.searchIconNavbarBuild{
    background-color: #febd69;
    font-size: 10px;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    cursor: pointer;
    padding: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    &:hover{
        background-color: #f1b05c;
    }
}

.searchIconNavbarBuildIcon{
    font-size: 20px;
    color: black;
}

/* ------ */

.rightSideNavbarBuild{
    padding: 10px;
    color: white;
    display: flex;
    gap: 10px;
    margin-left:-20px;
}
.indianFlagCode{
    display: flex;
    justify-content: center;
    align-items: center;
    padding:12px 5px;
    padding-bottom: 1px;
    gap: 5px;
    cursor: pointer;
    height: 30px;
    margin-top: -2px;
    &:hover{
        border-radius: 3px;
        border: 1px solid;
    }
}
.indiaFlag{
    width: 22px;
    height: 15px;
}
.indiaCodeNavbarBuild{
    font-size: 14px;
    display: flex;
    line-height: 10px;
    align-items: center;
    font-weight: 700;
}
.indiaCodeNavbarBuildDrop{
    color: #ccc;
}
.helloSignInNavbarBuild{
    display: flex;
    position: relative;
    color: white;
    text-decoration: none;
    flex-direction: column;
    height: 30px;
    padding:8px;
    margin-top: -5px;
    cursor: pointer;
    &:hover{
        border-radius: 3px;
        border: 1px solid;
    }
}
.helloTopNavbarBuild{
    font-size: 12px;
    font-weight: 500;
}

.cartTitle{
    font-size: 14px;
    font-weight: 600;
}
.cartItemNumberNavbarBuild{
    position: absolute;
    bottom:60%;
    left: 22%;
    color: orange;
    font-weight: 500;
    font-size: 16px;
}

@media screen and (max-width:1445px){
    .navbarBuildLocation{
        display: none;
    }
    .navbarBuildSearchBox{
        margin:0 auto;
    }
}
@media screen and (max-width:1140px){
   .sign{
    display: none;
   }
}
@media screen and (max-width:955px){
   .indianFlagCode{
    display: none;
   }
}
@media screen and (max-width:790px){
   .sign1{
    display: none;
   }
}
@media screen and (max-width:495px){
   .amazonLogoNavbar{
    width: 80px;
   }
   .navbarBuildSearchBox{
    margin-left: -20px;
   }
}
@media screen and (max-width:420px){
   .amazonLogoNavbar{
    width: 70px;
    margin-left: -10px;
   }
   .navbarBuildSearchBox{
    margin-left: -40px;
  
   }
   .cartTitle{
    display: none;
   }
}