.productPage{
    width: 100%;
}
.productTopBanner{
    display: flex;
    box-shadow:0 0 1px #747474 ;
    height: 50px;
    padding: 0 9px 0 15px;
    gap: 25px;
    
}
.productTopBannerItems{
padding: 0 13px;
display: flex;
justify-content: center;
align-items: center;
font-weight: 600;

}
.productTopBannerItemsSubMenu{
    padding: 0 13px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 12px;
    font-weight: 400;
    cursor: pointer;
    
    &:hover{
        text-decoration: underline;
        text-decoration-color:#f90 ;
        text-decoration-thickness: 2px ;
        /* border-bottom: 2px solid #f90; */
    }
}

.productsPageMain{
    display: flex;
    padding: 16px 8px;
  
}

.productsPageMainLeftCategory{
    width: 14%;
    border-right: 2px solid #ddd;
    padding: 0 10px;
}
.productsPageMainLeftCategoryTitle{
    font-size: 14px;
    font-weight: 700;
}

.productsPageMainLeftCategoryContent{
    margin-top: 10px;
    font-size: 14px;
    gap: 10px;
    font-weight: 450;
    display: flex;
    flex-direction: column;
    
   
}
.productsPageMainLeftCategoryContentSub{
    font-weight: 650;
}

.ratingLeftBox{
    display: flex;
    cursor: pointer;
    
}
.electro{
    margin-top: 10px;
    margin-left: 20px;
}
.electroitem{
    margin-left: 35px;
    margin-top: -6px;
    cursor: pointer;
    &:hover{
        color: rgba(142, 0, 0, 0.929);
    }
}
.andUp{
    font-size: 12px;
    font-weight: 400;
    margin-left: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    
    &:hover{
        color: rgba(142, 0, 0, 0.929);
    }
}
.boxhov:hover{
   color: #007185;
}
.boxicon{
    display: flex;
    
    gap: 5px;
    cursor: pointer;
}
.boxicon span:hover{
    color: rgba(142, 0, 0, 0.929);
}

.productsPageMainRight{
    width: 86%;
    padding: 0 0 12px;
}
.productsPageMainRightTopBanner{
    width: 99%;
    border: 1px solid #ccc;
    box-sizing: border-box;
    margin: 20px 0;
    padding: 14px 38px;
    border-radius: 5px;
    font-size: 14px;
}
.productsPageMainRightTopBannerSpan{
    color: #c45500;
    font-weight: 700;
}
.itemsImageProductPage{
    width: 100%;
    display: grid;
    gap: 30px;
    grid-template-columns: auto auto auto auto;
}
.itemsImageProductPageOne{
    display: flex;
    flex-direction: column;
    
    width: 100%;
    border-radius: 5px;
    box-sizing: border-box;
    height: 100%;
}
.imgBlockItemsImageProductPageOne{
    display: flex;
    justify-content: center;
    
    box-sizing: border-box;
    align-items: center;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    background-color: rgb(247,247,247);
    padding: 40px 20px;
}
.productImageProduct{
    width: 95%;
    height: 196px;
}
.productNameProduct{
    padding:0 20px 20px 20px;
    border: 1px solid #efeeee;
    border-top: none;
    display: flex;
    flex-direction: column;
    
}
.productNameProductRating{
    display: flex;
    margin: 10px 0;
    
}
.priceProductDetailPage{
    display: flex;
    
}
.currencyText{
    font-size: 13px;
}
.rateHomeDetail{
    font-size: 28px;
    font-weight: 500;
    display: flex;
    
    width: 100%;
    justify-content: space-between;
}
.addtobasketBtn{
    background-color: #FEBD69;
    width: fit-content;
    font-size: 14px;
    display: flex;
    
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 10px;
    border-radius: 5px;
    cursor: pointer;
}
.offProductPage{
    color: #727171;
    font-weight: 400;
}
.freeDeliveryHomePage{
    font-weight: 400;
    margin-top: 10px;
}

@media screen and (max-width:1400px){
    .itemsImageProductPage{
        grid-template-columns: auto auto auto;
    }
}
@media screen and (max-width:1000px){
    .itemsImageProductPage{
        grid-template-columns:auto auto;
        margin: 0 50px;
    }
    .productTopBannerItemsSubMenu{
        display: none;
    }
    .productsPageMainLeftCategory{
        display: none;
    }
}
@media screen and (max-width:670px){
    .itemsImageProductPage{
        grid-template-columns:auto;
        width: 70%;
        margin: 0 100px;
    }
  
}
@media screen and (max-width:450px){
    .itemsImageProductPage{
        grid-template-columns:auto;
        width: 100%;
        margin: 0 20px;
    }
  
}