.homeBanner{
    width: 100%;
    object-fit: contain;
    background-repeat: no-repeat;
    position: relative;
}
.homeBannerImg{
    height: 100%;
    width: 100%;
    mask-image: linear-gradient(to bottom,rgba(0,0,0,1),rgba(0, 0, 0, 0.100));
    z-index: -1;
}
.grayBackgroundHomeBanner{
    background-color: rgb(238,235,235);
    height: 80vh;
}
.homeBannerItemDiv{
    display: grid;
    grid-template-columns: auto auto auto auto;
    gap: 20px;
    top: 28%;
    padding: 0 20px;
  position:absolute;
  margin-right: 20px;
  margin-left: -10px;
  max-width: 100%;
  width: 100%;
  z-index: 1;
}
.homeBannerItemDivCard{
    display: flex;
    background-color: white;
    flex-direction: column;
    width: 343px;
    box-sizing: border-box;
    padding: 20px 0 15px;
}
.homeBannerItemDivCardTitle{
    margin: 0 20px;
    font-size: 21px;
    font-weight: 700;
}
.imgHomeBannerItemDivCard{
    margin: 10px 20px;
    display: grid;
    grid-template-columns: auto auto;
}
.imgBannerHomeDiv{
    height: 144px;
    width: 144px;
    display: flex;
    flex-direction: column;
}
.imgBannerHomeDivImg{
    width: 100%;
    cursor: pointer;
}
.imgBannerImgName{
    font-size: 12px;
    font-weight: 490;
    cursor: pointer;
}
.seeMoreHomeBanner{
    margin: 0 20px;
    font-size: 13px;
    color: #007185;
    font-weight: 600;
    cursor: pointer;
    &:hover{
        color: rgba(208, 52, 0, 0.733);
    }
}
.cardButton{
    margin: 15px 20px;
    border-radius: 20px;
    padding: 7px 0;
    background-color: #FFD814;
    border: none;
    font-weight: 500;
    font-size: 13px;
    cursor: pointer;
    &:hover{
        background-color: #ffc414fe;
    }
   
}
.cardRightImg{
    margin: 15px 25px;
    cursor: pointer;
}

@media screen and (max-width:1460px){
 .homeBannerItemDiv{
    grid-template-columns: auto auto auto;
    justify-content: center;
    gap: 50px;
 }
}
@media screen and (max-width:1200px){
 .homeBannerItemDiv{
    grid-template-columns: auto auto;
    justify-content: center;
    gap: 50px;
 }
}
@media screen and (max-width:800px){
 .homeBannerItemDiv{
    grid-template-columns: auto;
    justify-content: center;
 }
}